@import url(https://fonts.googleapis.com/css2?family=Shippori+Antique&display=swap);
body {
  margin: 0px;
  padding: 0px;
  height: 100%;
  width: 100%;
  font-family: 'Shippori Antique'
}
html {
  margin: 0px;
  padding: 0px;
  width:100%;
  height: 100%;
}
.item {
  margin: 3px;
  padding: 2px;
}
.container {
  display:flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.horizontal {
  flex-direction:row;
}
.vertical {
  flex-direction: column;
}
.spaceEvenly {
  justify-content: space-evenly;
}
.max {
  height: 100%;
  width: 100%;
}
.option:hover {
  cursor: pointer;
}
.wireframe {
  border: 1px solid black;
  box-sizing: border-box;
}
#root {
  height: 100%;
  width: 100%;
}
#BasicInfo {
  width: 100%;
  height: 15%;
}
#Picture {
  height: 13.5vh;
  aspect-ratio: 1 / 1;
  margin-left: 1%;
  margin-right: 1%;
}
#Name {
  text-align: left;
  font-size: 9.5vh;
  flex-grow: 1;
}
#InteractiveAccountView {
  margin-top: 5vh;
}
#SideMenu {
  align-self: flex-start;
}
#DisplayConfig {
  flex-grow: 2;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

